import Api from '@/js/api/baseApi';

export default class {
  static detail(callback, errorBack) {
    Api.request(Api.url() + 'enter_team/detail', callback, errorBack);
  }

  static save(data, callback, errorBack) {
    Api.request(Api.url() + 'enter_team/save', callback, errorBack, data, null);
  }

  static developmentDetail(data, callback, errorBack) {
    Api.request(Api.url() + 'development/detail', callback, errorBack, data, null);
  }

  static developmentSave(data, callback, errorBack) {
    Api.request(Api.url() + 'development/save', callback, errorBack, data, null);

  }

  static uri() {
    return Api.url() + 'enter_team/';
  }
}
