<template>
    <div style="text-align: left">
        <h3>团队信息编辑</h3>
        <Divider orientation="left">信息填写</Divider>
        <Row>
            <Col style="width: 500px">
                <Form :model="formData" :label-width="120">

                    <FormItem label="公司名称">
                        <Input v-model="formData.company" placeholder="请填写公司名称"></Input>
                    </FormItem>
                    <FormItem label="管理员账户">
                        <Input v-model="formData.admin_account" placeholder="请填写管理员账户"></Input>
                    </FormItem>
                    <FormItem label="营业执照">
                        <Input v-model="formData.business_license" placeholder="请填写营业执照编号"></Input>
                    </FormItem>
                    <FormItem label="营业执照扫描件">
                        <ImgSelect v-model="bl_picture" :url="formData.bl_picture">
                        </ImgSelect>
                    </FormItem>

                    <FormItem label="法人姓名">
                        <Input v-model="formData.legal_person" placeholder="请填写法人姓名"></Input>
                    </FormItem>
                    <FormItem label="法人身份证号">
                        <Input v-model="formData.id_card" placeholder="请填写法人身份证号"></Input>
                    </FormItem>

                    <FormItem label="身份证扫描件(正反面2张)">
                        <ImgsSelect v-model="id_card_pictures" :urls="formData.id_card_pictures" :max="2">
                        </ImgsSelect>
                    </FormItem>


                    <FormItem label="主营业务">
                        <Input v-model="formData.main_business" placeholder="请填写主营业务"></Input>
                    </FormItem>

                    <FormItem label="发展阶段">
                        <Input v-model="formData.develop_stage" placeholder="请填写发展阶段"></Input>
                    </FormItem>

                    <FormItem label="公司简介">
                        <Input :rows="4" type="textarea" v-model="formData.description"
                               placeholder="请填写公司简介"></Input>
                    </FormItem>
                    <FormItem label="公司 LOGO">
                        <ImgSelect v-model="logo" :url="formData.logo">
                        </ImgSelect>
                    </FormItem>

                    <FormItem>
                        <Button type="success" long @click="submit">提交</Button>
                    </FormItem>

                </Form>
                <Spin size="large" fix v-if="load"></Spin>
            </Col>

        </Row>
    </div>
</template>

<script>
  import api from './api';
  import ImgSelect from '@/components/imgSelect';
  import ImgsSelect from '@/components/imgsSelect';

  export default {
    name: 'edit',
    components: {
      ImgSelect,
      ImgsSelect
    },
    data() {
      return {
        formData: {},
        space: [],
        logo: null,
        id_card_pictures: null,
        load: false,
        times: [],
        bl_picture: null
      };
    },
    mounted() {
      this.loadData();
    },
    methods: {
      loadData() {
        this.load = true;
        api.detail(res => {
          this.load = false;
          this.formData = res.data.data;
        }, () => {
          this.load = false;
        });
      },
      submit() {
        if (this.logo == null && !this.$formUtil.isStrValid(this.formData.logo)) {
          this.$Message.error('请选择 logo');
          return;
        }
        if (this.bl_picture == null && !this.$formUtil.isStrValid(this.formData.bl_picture)) {
          this.$Message.error('请选择营业执照扫描件');
          return;
        }
        if (this.id_card_pictures == null && !this.$formUtil.isStrValid(this.formData.id_card_pictures)) {
          this.$Message.error('请选择 法人身份证正反面2张');
          return;
        } else if (this.id_card_pictures != null && this.id_card_pictures.length !== 2) {
          this.$Message.error('请选择 法人身份证正反面2张');
          return;
        }

        let data = this.$formUtil.objToFormData(this.formData);
        if (this.logo != null) data.append('logo', this.logo);
        if (this.bl_picture != null) data.append('bl_picture', this.bl_picture);
        if (this.id_card_pictures != null) {
          data.append('id_card_pictures[0]', this.id_card_pictures[0]);
          data.append('id_card_pictures[1]', this.id_card_pictures[1]);
        }
        this.load = true;
        api.save(data, () => {
          this.load = false;
          this.$router.push({ name: 'teamInfo' });
        }, () => {
          this.load = false;
        });
      }
    }
  };
</script>

